.introduction {
    user-select: none;
    background-color: #14213D;
    color: #fff;
    height: 100%;
    width: 50%;
    top: 0;
    left: 0;
    position: fixed;
    overflow: hidden;
    z-index: 10;
    transition: .75s ease;
    .introduction__menu {
        .introduction__modal {
            transition: .3s;
            z-index: 0;
            background-color: #fff;
            border-radius: 15px;
            padding: 15% 5% 5%;
            margin: 6%;
            position: absolute;
            width: 25vw;
            height: 52vh;
            display: flex;
            flex-direction: column;
            justify-content: space-evenly;
            align-items: flex-start;
            letter-spacing: 2px;
            white-space: nowrap;
            word-spacing: 15px;
            // TEST ANIMACJI
            opacity: 0;
            top: -100%;
            left: -100%;
            transform: translate(-50%, -50%) scale(0.01);
            transition: opacity 1000ms ease-in-out, top 300ms ease-in-out, left 300ms ease-in-out, transform 300ms ease-in-out;
            &.activeStyle {
                //TEST
                top: 25%;
                left: 25%;
                transform: translate(-50%, -50%) scale(1);
                opacity: 1;
                transition: transform 300ms cubic-bezier(0.18, 0.89, 0.43, 1.19);
            }
        }
        li {
            list-style: none;
            font-size: 35px;
            padding: 8% 0 0;
            text-transform: uppercase;
            font-weight: bold;
        }
        li:first-of-type {
            padding-top: 3%;
        }
        a {
            color: #14213D;
            text-decoration: none;
        }
        .introduction__icon {
            color: #fff;
            transition: .05s ease-in-out; //animacje dodać
            cursor: pointer;
            font-size: 60px;
            position: absolute;
            left: 11.5%;
            top: 8%;
            &.activeStyle {
                left: 12%;
                color: #14213D;
                font-size: 70px;
            }
        }
    }
    .introduction__info {
        transition: .25s;
        text-align: left;
        font-size: 45px;
        position: absolute;
        left: 10%;
        bottom: 40%;
        &.activeStyle {
            bottom: 11%;
        }
        a {
            text-decoration: none;
            button {
                color: #fff;
                border: 2px solid #fff;
                text-transform: uppercase;
                background-color: transparent;
                font-size: 20px;
                margin-top: 45px;
                padding: 15px;
                cursor: pointer;
                &:hover {
                    color: #14213D;
                    background-color: #fff;
                }
            }
        }
    }
}