.contact {
    background: #E5E5E5;
    background-color: #495057;
    background-color: #343a40;
    color: #000;
    color: #fff;
    padding: 4vh 40px;
    user-select: none;
    text-transform: uppercase;
    .contact__links {
        width: 45%;
        text-transform: none;
    }
    a {
        text-decoration: none;
        color: #fff;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}

.contact__item {
    user-select: text;
    padding: 7.5% 5%;
    .contact__heading {
        text-transform: uppercase;
        font-weight: bold;
        letter-spacing: 2px;
        font-size: 28px;
        padding-bottom: 5px;
    }
    &:nth-of-type(3) svg {
        border-radius: 5px;
    }
}

.contact__section {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
}

.cv__btn {
    color: #fff;
    border: 2px solid #fff;
    text-transform: uppercase;
    background-color: transparent;
    font-size: 20px;
    padding: 15px 25px;
    cursor: pointer;
    display: flex;
    margin: 12vh auto 0;
    &:hover {
        background-color: #FCA311;
        border: 2px solid #FCA311;
    }
    &:hover .download__cv {
        color: #000;
    }
}

.contact__icons {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 7.5vh;
    a {
        margin: 0 2vw;
        &:first-of-type:hover {
            color: #9acd32;
        }
        &:nth-of-type(2):hover {
            color: #FCA311;
        }
        &:nth-of-type(3):hover {
            svg {
                border-radius: 5px;
                outline: none;
                stroke: none;
                background: #fff;
                fill: #4169e1;
            }
        }
        &:nth-of-type(4):hover {
            color: #000;
        }
    }
    .icon {
        font-size: 35px;
        transition: .2s;
    }
}