.skills {
    overflow: hidden;
    position: sticky;
    top: 0;
    padding: 35px 50px;
    text-align: left;
    color: #000;
    font-size: 23px;
    background-color: #E5E5E5;
    z-index: 3;
    min-height: 200vh;
    min-height: 100vh;
    height: auto;
    h1 {
        color: #14213D;
        font-size: 45px;
        margin: 0 0 7% 5%; //7% --> 10% na mobilce
        text-transform: uppercase;
    }
    .row__technologies {
        user-select: none;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        align-content: space-around;
        height: 75vh;
        .skill__example {
            position: relative;
            &:hover p {
                opacity: 0;
                transition: .1s;
            }
            &:hover .skill {
                transition: .1s;
                opacity: 1 !important;
            }
            .skill {
                transition: 3s ease;
                width: 13.5vh;
                height: 13.5vh;
                margin: 15px 45px;
                background-position: center;
                background-size: contain;
                background-repeat: no-repeat;
                opacity: 0.375;
            }
            p {
                opacity: 1;
                transition-delay: 2.25s;
                transition-property: .1s;
                text-align: center;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                font-size: 2.5rem;
            }
        }
        .skill__example:nth-of-type(1) .skill {
            background-image: url(/img/HTML.png);
        }
        .skill__example:nth-of-type(2) .skill {
            background-image: url(/img/CSS.png);
        }
        .skill__example:nth-of-type(3) .skill {
            background-image: url(/img/SASS.png);
            opacity: 0.425;
        }
        .skill__example:nth-of-type(4) .skill {
            background-image: url(/img/JScript.png);
            opacity: 0.425;
        }
        .skill__example:nth-of-type(5) .skill {
            background-image: url(/img/React.png);
        }
        .skill__example:nth-of-type(6) .skill {
            background-image: url(/img/GIT.png);
            opacity: 0.45;
        }
        .skill__example:nth-of-type(7) .skill {
            background-image: url(/img/RWD.png);
        }
        .skill__example:nth-of-type(8) .skill {
            background-image: url(/img/ReactRouter.png);
            opacity: 0.25;
        }
        .skill__example:nth-of-type(9) .skill {
            background-image: url(/img/Redux.png);
        }
    }
}