.content {
    position: absolute;
    width: 50%;
    right: 0;
    top: 0;
    transition: .75s ease-in;
    overflow: unset;
    &.fixNav {
        overflow: hidden;
    }
}

.profile__picture {
    user-select: none;
    z-index: -1;
    position: sticky;
    height: 100vh;
    width: calc(50vw - (50vw - 100%) / 2);
    object-fit: cover;
    top: 0;
    right: 0;
    transition: filter 750ms ease, opacity 1500ms ease;
    opacity: 1;
    filter: saturate(0) brightness(1) blur(0px);
}

.about__me,
.projects,
.contact,
.section__map {
    overflow: hidden;
    position: sticky;
    top: 0;
    height: 100vh;
    padding: 4vh 50px;
    text-align: left;
    color: #14213D;
    font-size: 23px;
    h1 {
        font-size: 45px;
        margin: 0 0 5% 5%;
        text-transform: uppercase;
    }
}

.about__me {
    background-color: #fff;
    z-index: 2;
}

.projects {
    background-color: #FCA311;
    z-index: 4;
}

.contact {
    background-color: #fff;
    z-index: 5;
}